import Image from "next/image"
import { Button } from "@/components/ui/button"
import { ArrowRight } from "lucide-react"

export default function PharmacyHowItWorks() {
  const steps = [
    {
      number: "01",
      title: "Upload Prescription or Select Medications",
      description: "Take a photo of your prescription or browse our catalog to select over-the-counter medications.",
      image: "/placeholder.svg?height=300&width=400&text=Upload+Prescription",
    },
    {
      number: "02",
      title: "Review Order and Payment",
      description: "Confirm your medication order, delivery address, and complete payment through our secure system.",
      image: "/placeholder.svg?height=300&width=400&text=Review+Order",
    },
    {
      number: "03",
      title: "Pharmacist Verification",
      description: "A licensed pharmacist reviews your order and prescription to ensure accuracy and safety.",
      image: "/placeholder.svg?height=300&width=400&text=Pharmacist+Verification",
    },
    {
      number: "04",
      title: "Delivery to Your Location",
      description:
        "Your medications are packaged securely and delivered directly to your doorstep by our trusted couriers.",
      image: "/placeholder.svg?height=300&width=400&text=Medication+Delivery",
    },
  ]

  return (
    <section className="bg-gray-50 py-20 dark:bg-gray-900">
      <div className="container mx-auto px-4">
        <div className="mx-auto mb-16 max-w-3xl text-center">
          <h2 className="mb-4 text-3xl font-bold text-gray-900 md:text-4xl dark:text-white">
            How Our <span className="text-purple-600 dark:text-purple-400">Pharmacy Service</span> Works
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300">
            Getting your medications has never been easier. Follow these simple steps to order and receive your
            prescriptions.
          </p>
        </div>

        <div className="space-y-20">
          {steps.map((step, index) => (
            <div
              key={index}
              className={`flex flex-col items-center gap-12 ${index % 2 === 0 ? "lg:flex-row" : "lg:flex-row-reverse"}`}
            >
              <div className="w-full lg:w-1/2">
                <div className="relative">
                  <div className="absolute -top-4 -left-4 z-0 h-24 w-24 rounded-full bg-purple-100 dark:bg-purple-900/30"></div>
                  <div className="relative z-10 overflow-hidden rounded-xl bg-white shadow-xl dark:bg-gray-800">
                    <Image
                      src={step.image || "/placeholder.svg"}
                      alt={step.title}
                      width={500}
                      height={375}
                      className="h-auto w-full object-cover"
                    />
                  </div>
                  <div className="absolute -bottom-6 -right-6 z-0 h-32 w-32 rounded-full bg-blue-50 dark:bg-blue-900/30"></div>
                </div>
              </div>

              <div className="w-full space-y-6 lg:w-1/2">
                <div className="inline-flex h-16 w-16 items-center justify-center rounded-full bg-purple-600 text-xl font-bold text-white">
                  {step.number}
                </div>
                <h3 className="text-2xl font-bold text-gray-900 md:text-3xl dark:text-white">{step.title}</h3>
                <p className="text-xl text-gray-600 dark:text-gray-300">{step.description}</p>

                {index === steps.length - 1 && (
                  <Button className="mt-6 bg-purple-600 hover:bg-purple-700">
                    Order Now <ArrowRight className="ml-2 h-4 w-4" />
                  </Button>
                )}
              </div>
            </div>
          ))}
        </div>

        <div className="mt-16 text-center">
          <p className="mb-6 text-gray-600 dark:text-gray-300">
            Need help with your medication order? Our support team is available 24/7.
          </p>
          <Button variant="outline" className="border-purple-600 text-purple-600 hover:bg-purple-50 dark:border-purple-400 dark:text-purple-400 dark:hover:bg-purple-900/30">
            Contact Support
          </Button>
        </div>
      </div>
    </section>
  )
}
