import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Pill, ShoppingCart, Heart, Star } from "lucide-react"

export default function PharmacyProducts() {
  const categories = [
    "Prescription Medications",
    "Over-the-Counter",
    "Chronic Conditions",
    "Vitamins & Supplements",
    "Personal Care",
    "Medical Devices",
    "First Aid",
    "Baby & Maternal",
  ]

  const featuredProducts = [
    {
      name: "Blood Pressure Monitor",
      category: "Medical Devices",
      image: "/placeholder.svg?height=200&width=200&text=BP+Monitor",
      price: "RWF 45,000",
      rating: 4.8,
      reviews: 124,
    },
    {
      name: "Multivitamin Complex",
      category: "Vitamins & Supplements",
      image: "/placeholder.svg?height=200&width=200&text=Multivitamin",
      price: "RWF 12,500",
      rating: 4.6,
      reviews: 89,
    },
    {
      name: "Diabetes Test Strips",
      category: "Chronic Conditions",
      image: "/placeholder.svg?height=200&width=200&text=Test+Strips",
      price: "RWF 18,000",
      rating: 4.9,
      reviews: 203,
    },
    {
      name: "First Aid Kit",
      category: "First Aid",
      image: "/placeholder.svg?height=200&width=200&text=First+Aid+Kit",
      price: "RWF 22,000",
      rating: 4.7,
      reviews: 156,
    },
  ]

  return (
    <section className="bg-gray-50 py-20 dark:bg-gray-900">
      <div className="container mx-auto px-4">
        <div className="mx-auto mb-16 max-w-3xl text-center">
          <h2 className="mb-4 text-3xl font-bold text-gray-900 md:text-4xl dark:text-white">
            Browse Our <span className="text-purple-600 dark:text-purple-400">Product Categories</span>
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300">
            From prescription medications to healthcare essentials, we offer a comprehensive range of products for all
            your health needs.
          </p>
        </div>

        {/* Categories */}
        <div className="mb-16 flex flex-wrap justify-center gap-4">
          {categories.map((category, index) => (
            <button
              key={index}
              className="rounded-full border border-gray-200 bg-white px-6 py-3 font-medium text-gray-800 shadow-sm transition-shadow duration-300 hover:shadow-md dark:border-gray-700 dark:bg-gray-800 dark:text-gray-200"
            >
              {category}
            </button>
          ))}
        </div>

        {/* Featured Products */}
        <h3 className="mb-8 text-2xl font-bold text-gray-900 dark:text-white">Featured Products</h3>
        <div className="grid gap-8 sm:grid-cols-2 lg:grid-cols-4">
          {featuredProducts.map((product, index) => (
            <div
              key={index}
              className="overflow-hidden rounded-xl bg-white shadow-md transition-shadow duration-300 hover:shadow-lg dark:bg-gray-800"
            >
              <div className="relative">
                <div className="relative aspect-square">
                  <Image
                    src={product.image || "/placeholder.svg"}
                    alt={product.name}
                    fill
                    className="object-cover p-4"
                  />
                </div>
                <button className="absolute right-4 top-4 flex h-8 w-8 items-center justify-center rounded-full bg-white shadow-md dark:bg-gray-700">
                  <Heart className="h-4 w-4 text-gray-400 hover:text-red-500 dark:text-gray-300" />
                </button>
              </div>

              <div className="p-6">
                <div className="mb-2 text-sm font-medium text-purple-600 dark:text-purple-400">{product.category}</div>
                <h4 className="mb-2 text-lg font-semibold text-gray-900 dark:text-white">{product.name}</h4>

                <div className="mb-4 flex items-center">
                  <div className="flex items-center">
                    <Star className="h-4 w-4 fill-current text-yellow-400" />
                    <span className="ml-1 text-sm font-medium text-gray-600 dark:text-gray-300">{product.rating}</span>
                  </div>
                  <span className="mx-2 text-gray-300 dark:text-gray-600">|</span>
                  <span className="text-sm text-gray-600 dark:text-gray-300">{product.reviews} reviews</span>
                </div>

                <div className="flex items-center justify-between">
                  <span className="text-lg font-bold text-gray-900 dark:text-white">{product.price}</span>
                  <Button size="sm" className="bg-purple-600 hover:bg-purple-700">
                    <ShoppingCart className="mr-1 h-4 w-4" /> Add
                  </Button>
                </div>
              </div>
            </div>
          ))}
        </div>

        <div className="mt-12 text-center">
          <Button className="bg-purple-600 hover:bg-purple-700">
            View All Products <Pill className="ml-2 h-4 w-4" />
          </Button>
        </div>
      </div>
    </section>
  )
}
